////
/// File block.
///
/// @group  Blocks
/// @author Bryan Hoffman <bryan@spigotdesign.com>
/// @link   https://spigotdesign.com/
////

.wp-block-file {
	//margin-bottom: $spacing-global;
	margin-top: $spacing-global;

	&.aligncenter {
		text-align: center;
	}

	&.alignright {
		text-align: right;
	}

	// See `components/_button.scss`.
	&__button {}

	&__button:hover,
	&__button:focus {
		color:  var( --color-content-background );
	}
}

// Give the file block button some spacing to the left if there's another element
// preceding it.
* + .wp-block-file__button {
	margin-left: 0.75em;
}



