// Promo Bar Styles

// Header Push
.app-header {
    .page-template-landing.admin-bar.dwpb-push-page & {
        @include mq(desktop-nav) {
            top: 0;
        }
    }
}

.dwpb-push-page {
    &:not(.dwpb-allow-close):not(.dwpb-open):not(.admin-bar) {

    	@include mq(mobile-nav) {
            .menu--secondary {
                padding-right: $margin * 1.5;
            }
        }
        
        @include mq(desktop-nav) {
            .menu--secondary {
                padding-right: $margin;
            }
        }
    }

    &not(.dwpb-open) {
        
    }
}

.dwpb-allow-close {}

.dwpb-open {}

.fl-builder-edit {
    .dwpb-action, #dwpb {
        display: none;
    }
}


